






export default {
  name:     'VueBadge',
  props:    {
    primary:  {
      type: Boolean,
    },
    accent:   {
      type: Boolean,
    },
    warn:     {
      type: Boolean,
    },
  },
  computed: {
    cssClasses() {
      const classes: string[] = [this.$style.vueBadge];

      if (this.primary) {
        classes.push(this.$style.primary);
      } else if (this.accent) {
        classes.push(this.$style.accent);
      } else if (this.warn) {
        classes.push(this.$style.warn);
      }

      return classes;
    },
  },
};
