
















import VueCollapse from '../../VueCollapse/VueCollapse.vue';

export default {
  name:       'VueAccordionItem',
  components: {
    VueCollapse,
  },
  props:      {
    title:    {
      type:     String,
      required: true,
    },
    initOpen: {
      type:     Boolean,
      required: false,
      default:  false,
    },
  },
  data(): any {
    return {
      idx:  null,
      open: false,
    };
  },
  computed:   {
    show() {
      return this.open;
    },
    iconClasses() {
      const classes = [this.$style.icon];

      if (this.show) {
        classes.push(this.$style.open);
      }

      return classes;
    },
  },
  methods:    {
    click() {
      this.$parent.openItem(this.idx);
    },
  },
  created() {
    if (this.$parent.register) {
      this.$parent.register(this);
    }
  },
};
