











export default {
  name:       'VueTooltip',
  props:      {
    tip:      {
      type:     String,
      required: true,
    },
  },
  data(): any {
    return {
      show: false,
    };
  },
  computed:   {
    cssClasses() {
      const classes: string [] = [this.$style.vueTooltip];

      if (this.show) {
        classes.push(this.$style.show);
      }

      if (this.$slots.default && this.$slots.default[0].tag === undefined) {
        classes.push(this.$style.highlight);
      }

      return classes;
    },
  },
  methods:    {
    onEnter() {
      this.show = true;
    },
    onLeave() {
      this.show = false;
    },
    onTouchEnd() {
      this.show = !this.show;
    },
  },
};
