















export default {
  name:     'VueLoader',
  props:    {
    medium:   {
      type:     Boolean,
      required: false,
      default:  false,
    },
    large:    {
      type:     Boolean,
      required: false,
      default:  false,
    },
    primary:  {
      type:     Boolean,
      required: false,
      default:  false,
    },
    accent:   {
      type:     Boolean,
      required: false,
      default:  false,
    },
    warn:     {
      type:     Boolean,
      required: false,
      default:  false,
    },
  },
  computed: {
    cssClasses() {
      const classes = [this.$style.loader];

      if (this.medium) {
        classes.push(this.$style.medium);
      }

      if (this.large) {
        classes.push(this.$style.large);
      }

      if (this.primary) {
        classes.push(this.$style.primary);
      }

      if (this.accent) {
        classes.push(this.$style.accent);
      }

      if (this.warn) {
        classes.push(this.$style.warn);
      }

      return classes;
    },
  },
};
