






export default {
  name:     'VueGridItem',
  props:    {
    fill:     {
      type:     Boolean,
      required: false,
      default:  false,
    },
  },
  computed: {
    cssClasses() {
      const classes = [this.$style.gridItem];

      if (this.fill) {
        classes.push(this.$style.fill);
      }

      return classes;
    },
  },
};
