




























import VueInput    from '../VueInput/VueInput.vue';
import VueModal    from '../VueModal/VueModal.vue';
import VueCalendar from '../VueCalendar/VueCalendar.vue';

export default {
  name:       'VueDatePicker',
  components: {
    VueInput,
    VueModal,
    VueCalendar,
  },
  props:      {
    name:           {
      type:    String,
      default: 'date',
    },
    id:             {
      type:    String,
      default: 'date',
    },
    minDate:        {
      type:     Date,
      required: false,
    },
    maxDate:        {
      type:     Date,
      required: false,
    },
    firstDayOfWeek: {
      type:     Number,
      required: false,
      default:  0,
    },
    placeholder:    {
      type:     String,
      required: false,
      default:  '',
    },
    startDate:      {
      type:     Date,
      required: false,
    },
    endDate:        {
      type:     Date,
      required: false,
    },
    currentDate:    {
      type:     Date,
      required: false,
    },
    required:       {
      type:    Boolean,
      default: false,
    },
    disabled:       {
      type:    Boolean,
      default: false,
    },
    readonly:       {
      type:    Boolean,
      default: true,
    },
    message:        {
      type:    String,
      default: '',
    },
  },
  data(): any {
    return {
      show:         false,
      selectedDate: null,
    };
  },
  computed:   {
    inputValue() {
      return this.date === null ? '' : this.$d(this.date, 'datePicker');
    },
    date() {
      if (this.currentDate) {
        return this.currentDate;
      }

      return this.selectedDate;
    },
  },
  methods:    {
    onFocus(e: any) {
      e.currentTarget.blur();
      this.show = true;
    },
    onChange(date: Date): void {
      this.selectedDate = date;
      this.$emit('change', this.selectedDate);
    },
  },
};
