
















export default {
  name:     'VueCheckbox',
  props:    {
    name:     {
      type:     String,
      required: true,
    },
    id:       {
      type:     String,
      required: true,
    },
    checked:  {
      type:    Boolean,
      default: false,
    },
    value:    {
      type:    Boolean,
      default: false,
    },
    disabled: {
      type:    Boolean,
      default: false,
    },
    required: {
      type:    Boolean,
      default: false,
    },
    radio:    {
      type:    Boolean,
      default: false,
    },
    label:    {
      type:     String,
      required: true,
    },
  },
  computed: {
    cssClasses() {
      const classes = [];

      if (this.radio) {
        classes.push(this.$style.radio);
      } else {
        classes.push(this.$style.checkbox);
      }

      if (this.disabled) {
        classes.push(this.$style.disabled);
      }

      return classes;
    },
    inputType() {
      if (this.radio) {
        return 'radio';
      } else {
        return 'checkbox';
      }
    },
  },
  methods:  {
    onClick(e: Event) {
      e.preventDefault();

      if (this.disabled === false) {
        this.$emit('click', e);
        this.$emit('input', !Boolean(this.value));
      }
    },
  },
};
