









































import FadeAnimation from '../../animations/FadeAnimation/FadeAnimation.vue';

export interface ICarouselImage {
  copyright?: string;
  alt?: string;
  url: string;
}

export default {
  name:       'VueCarousel',
  components: { FadeAnimation },
  props:      {
    images:        {
      type:    Array,
      default: (): any[] => [],
    },
    interval:      {
      type:    Number,
      default: 5000,
    },
    activeSlide:   {
      type:    Number,
      default: 1,
    },
    minHeight:     {
      type:    Number,
      default: 500,
    },
    showIndicator: {
      type:    Boolean,
      default: true,
    },
  },
  data(): any {
    return {
      currentSlide:     this.activeSlide - 1,
      maxSlides:        this.images.length - 1,
      intervalInstance: null,
      pause:            false,
      preloadedImages:  [],
    };
  },
  methods:    {
    isActiveSlide(idx: number) {
      return this.currentSlide === idx;
    },
    preloadImages() {
      this.images.forEach((image: ICarouselImage) => {
        let imageInstance: HTMLImageElement = new Image();

        imageInstance.src = image.url;
        imageInstance.alt = image.alt;
        imageInstance.title = image.copyright;

        this.preloadedImages.push(imageInstance);
      });
      this.setInterval();
    },
    changeSlide() {
      if (this.pause) {
        return;
      }
      if (this.currentSlide === this.maxSlides) {
        this.currentSlide = 0;
      } else {
        this.currentSlide += 1;
      }
    },
    setInterval() {
      if (this.images.length <= 1) {
        return;
      }
      this.intervalInstance = setInterval(this.changeSlide, this.interval);
    },
  },
  mounted() {
    this.preloadImages();
  },
  beforeDestroy() {
    clearInterval(this.intervalInstance);
  },
};
