











import VueLoader from '../VueLoader/VueLoader.vue';

export default {
  name:       'VueButton',
  props:      {
    primary:  {
      type:     Boolean,
      required: false,
      default:  false,
    },
    accent:   {
      type:     Boolean,
      required: false,
      default:  false,
    },
    warn:     {
      type:     Boolean,
      required: false,
      default:  false,
    },
    disabled: {
      type:     Boolean,
      required: false,
      default:  false,
    },
    loading:  {
      type:     Boolean,
      required: false,
      default:  false,
    },
    pulse:    {
      type:     Boolean,
      required: false,
      default:  false,
    },
  },
  components: {
    VueLoader,
  },
  methods:    {
    onClick(e: Event) {
      if (this.disabled === false && this.loading === false) {
        this.$emit('click', e);
      }
    },
  },
  computed:   {
    cssClasses() {
      const classes = [this.$style.button, this.$style.ripple];

      if (this.primary) {
        classes.push(this.$style.primary);
      }

      if (this.accent) {
        classes.push(this.$style.accent);
      }

      if (this.warn) {
        classes.push(this.$style.warn);
      }

      if (this.disabled === true || this.loading === true) {
        classes.push(this.$style.disabled);
      }

      if (this.pulse) {
        classes.push(this.$style.pulse);
      }

      return classes;
    },
  },
};
