





















export default {
  name:     'VueTabGroup',
  data(): any {
    return {
      tabs:       [],
      currentTab: 0,
      tabHeader:  [],
    };
  },
  computed: {
    headerItems() {
      return this.tabHeader;
    },
  },
  methods:  {
    changeTab(idx: number) {
      this.currentTab = idx;
      this.handleTabs();
    },
    handleTabs() {
      this.tabs.forEach((tab: any) => {
        tab.$data.active = tab.$data.idx === this.currentTab;
      });
    },
    register(tab: any) {
      tab.$data.idx = this.tabs.length;

      this.tabs.push(tab);
      this.tabHeader.push({
                            idx:   tab.$data.idx,
                            title: tab.$props.title,
                            icon:  tab.$props.icon,
                          });

      this.handleTabs();
    },
  },
  beforeDestroy() {
    this.tabs = [];
    this.tabHeader = [];
    this.currentTab = 0;
  },
};
