




















export default {
  name:     'VuePagination',
  props:    {
    pages:   {
      type:     Number,
      required: true,
    },
    current: {
      type:     Number,
      required: true,
    },
  },
  data(): any {
    return {
      t: window['$t'].bind(window['i18n']),
    };
  },

  computed: {
    prevCssClasses() {
      const classes = [this.$style.prev];

      if (this.current <= 1) {
        classes.push(this.$style.disabled);
      }

      return classes;
    },
    nextCssClasses() {
      const classes = [this.$style.next];

      if (this.current >= this.pages) {
        classes.push(this.$style.disabled);
      }

      return classes;
    },
  },
  methods:  {
    prevClick() {
      if (this.current > 1) {
        this.$emit('change', this.current - 1);
      }
    },
    nextClick() {
      if (this.current < this.pages) {
        this.$emit('change', this.current + 1);
      }
    },
  },
};
