
























export default {
  name:     'VueToggle',
  props:    {
    name:     {
      type:     String,
      required: true,
    },
    id:       {
      type:     String,
      required: true,
    },
    checked:  {
      type:    Boolean,
      default: false,
    },
    value:    {
      type:    Boolean,
      default: false,
    },
    disabled: {
      type:    Boolean,
      default: false,
    },
    required: {
      type:    Boolean,
      default: false,
    },
    label:    {
      type:     String,
      required: true,
    },
  },
  data(): any {
    return {
      focus: false,
    };
  },
  computed: {
    isChecked() {
      return this.checked || this.value;
    },
    cssClasses() {
      const classes: string[] = [this.$style.vueToggle];

      if (this.focus) {
        classes.push(this.$style.focus);
      }

      if (this.isChecked) {
        classes.push(this.$style.checked);
      }

      if (this.disabled) {
        classes.push(this.$style.disabled);
      }

      return classes;
    },
  },
  methods:  {
    onClick(e: Event) {
      e.preventDefault();
      this.$refs.input.focus();

      if (this.disabled === false) {
        this.$emit('click', e);
        this.$emit('input', !Boolean(this.value));
      }
    },
  },
};
