










export default {
  name:       'VuePanelHeader',
  props:      {
    title:    {
      type:     String,
      required: false,
      default:  '',
    },
    subtitle: {
      type:     String,
      required: false,
      default:  '',
    },
    image:    {
      type:     String,
      required: false,
      default:  '',
    },
  },
  computed:   {
    cssClasses() {
      const classes = ['vue-panel-header', this.$style.vuePanelHeader];

      if (this.image) {
        classes.push(this.$style.withImage);
      }

      return classes;
    },
  },
  methods:    {},
};
