












import anime from 'animejs';

export default {
  name:    'VueModal',
  props:   {
    show:       {
      required: false,
      default:  false,
      type:     Boolean,
    },
    fitContent: {
      type:     Boolean,
      required: false,
      default:  false,
    },
  },
  methods: {
    beforeEnter(el: HTMLElement) {
      el.style.transform = 'translateY(100%)';

      document.getElementById('overlay').style.visibility = 'visible';
    },
    enter(el: HTMLElement, done: any) {
      document.getElementById('overlay').style.opacity = '0.5';
      document.body.style.overflow = 'hidden';

      anime({
              targets:    el,
              translateY: {
                value:      '0%',
                duration:   250,
                elasticity: 100,
              },
              complete:   done,
            });
    },
    beforeLeave(el: HTMLElement) {
      el.style.transform = 'translateY(0%)';
    },
    leave(el: HTMLElement, done: any) {
      const overlay: HTMLElement = document.getElementById('overlay');
      overlay.style.opacity = '0';
      document.body.style.overflow = 'initial';

      anime({
              targets:    el,
              translateY: {
                value:      '200%',
                duration:   250,
                elasticity: 100,
              },
              complete() {
                overlay.style.visibility = 'hidden';
                done();
              },
            });
    },
    handleDocumentClick(e: Event) {
      if (this.$refs.modal && this.$refs.modal.contains(e.target) === false) {
        this.$emit('close');
      }
    },
  },
  beforeMount() {
    let overlay: HTMLElement = document.getElementById('overlay');

    if (overlay === null) {
      overlay = document.createElement('div');
      overlay.id = 'overlay';
      overlay.style.position = 'fixed';
      overlay.style.top = '0';
      overlay.style.left = '0';
      overlay.style.width = '100%';
      overlay.style.height = '100%';
      overlay.style.zIndex = '2000';
      overlay.style.background = '#000';
      overlay.style.opacity = '0';
      overlay.style.visibility = 'hidden';
      overlay.style.transition = 'opacity 250ms linear';
      document.body.appendChild(overlay);
    }

    document.addEventListener('mousedown', this.handleDocumentClick);
    document.addEventListener('touchstart', this.handleDocumentClick);
  },
  beforeDestroy() {
    document.removeEventListener('mousedown', this.handleDocumentClick);
    document.removeEventListener('touchstart', this.handleDocumentClick);
  },
};
