



























import { Validator } from 'vee-validate';

export default {
  name:     'VueInput',
  inject:   {
    $validator: {
      default: new Validator({}, {}),
    },
  },
  props:    {
    name:         {
      type:     String,
      required: true,
    },
    id:           {
      type:     String,
      required: true,
    },
    placeholder:  {
      type:    String,
      default: '',
    },
    required:     {
      type:    Boolean,
      default: false,
    },
    value:        {
      type:    String,
      default: '',
    },
    type:         {
      type:    String,
      default: 'text',
    },
    disabled:     {
      type:    Boolean,
      default: false,
    },
    readonly:     {
      type:    Boolean,
      default: false,
    },
    message:      {
      type:    String,
      default: '',
    },
    errorMessage: {
      type:    String,
      default: '',
    },
    validation:   {
      default: '',
    },
  },
  computed: {
    isValid() {
      return this.errors ? this.errors.first(this.name) === null : true;
    },
    messageOrError() {
      return this.isValid ? this.message : this.errorMessage;
    },
    cssClasses() {
      const classes = [this.$style.vueInput];

      if (this.disabled) {
        classes.push(this.$style.disabled);
      }

      if (!this.isValid) {
        classes.push(this.$style.error);
      }

      return classes;
    },
    handlers() {
      delete this.$listeners.input;

      return {
        ...this.$listeners,
        input: (e: any) => {
          this.$emit('input', e.target.value);
        },
      };
    },
  },
};
