












import { IComputedDataRowColumn } from '../IDataTable';

export default {
  name:    'VueDataTableRow',
  props:   {
    row:         {
      type:     Array,
      required: true,
    },
    columnWidth: {
      type:     String,
      required: true,
    },
  },
  methods: {
    onClick() {
      const row: any = {};

      this.row.forEach((column: IComputedDataRowColumn) => {
        row[column.key] = column.value;
      });

      this.$emit('click', row);
    },
  },
};
