

























import VueDatePicker from '../VueDatePicker/VueDatePicker.vue';

export default {
  name:       'VueDateRangePicker',
  components: {
    VueDatePicker,
  },
  props:      {
    minDate:          {
      type:     Date,
      required: false,
    },
    maxDate:          {
      type:     Date,
      required: false,
    },
    firstDayOfWeek:   {
      type:     Number,
      required: false,
      default:  0,
    },
    placeholderStart: {
      type:     String,
      required: false,
      default:  '',
    },
    placeholderEnd:   {
      type:     String,
      required: false,
      default:  '',
    },
  },
  data(): any {
    return {
      startDate: null,
      endDate:   null,
    };
  },
  methods:    {
    onStartChange(startDate: Date) {
      this.startDate = startDate;

      if (this.endDate && this.endDate.getTime() < this.startDate.getTime()) {
        this.endDate = this.startDate;
      }
    },
    onEndChange(endDate: Date) {
      this.endDate = endDate;
      this.$emit('change', [this.startDate, this.endDate]);
    },
  },
};
